import React, {useState, useCallback, useEffect} from 'react';
import TelegramScreen from "../../components/kit/Screen/TelegramScreen";
import TelegramHeader from "../../components/kit/Header/TelegramHeader";
import TelegramText from "../../components/kit/Text/TelegramText";
import AllTabs from "../../components/kit/Tabs/AllTabs";
import { useTonWallet, useTonConnectUI } from '@tonconnect/ui-react';
// import { Address } from '@ton/core';


const WalletScreen = () => {

    const wallet = useTonWallet();
    const [tonConnectUI, setOptions] = useTonConnectUI()
    const [tonWalletAddress, setTonWalletAddress] = useState(null)
    const [isLoading, setIsLoading] = useState(true)

    const transaction = {
        messages: [
            {
                address: "0:95e5d2850799068964d88b6881b8e9e75b37d3f7b05f78a0579e3bed88dab827", // destination address
                amount: "10000000" //Toncoin in nanotons
            }
        ]
    
    }


    // const rawAddress = '95e5d2850799068964d88b6881b8e9e75b37d3f7b05f78a0579e3bed88dab827';
    // const workchain = 0;
    
    // const address = new Address(workchain, Buffer.from(rawAddress, 'hex'));
    
    // const friendlyAddress = address.toString({
    //   testOnly: false,
    //   bounceable: true,
    // });
    
    // console.log("FRIENDLY:",friendlyAddress);


    const handleWalletConnect = useCallback(address => {
        setTonWalletAddress(address)
        // console.log("Wallet Connected")
        setIsLoading(false)
    }, [])

    const handleWalletDisconnect = useCallback(address => {
        setTonWalletAddress(null)
        // console.log("Wallet Disonnected")
        setIsLoading(false)
    }, [])

    useEffect(() => {
        const checkWalletConnection = async () => {
            if (tonConnectUI.account?.address) {
                handleWalletConnect(tonConnectUI.account?.address)
            } else {
                handleWalletDisconnect()
            }
        }

        checkWalletConnection()

        const unsubscribe = tonConnectUI.onStatusChange(wallet => {
            if (wallet) {
                handleWalletConnect(wallet.account.address)
            } else {
                handleWalletDisconnect()
            }
        })

        return () => {
            unsubscribe()
        }

    }, [tonConnectUI,handleWalletConnect,handleWalletDisconnect])

    const handleWalletAction = async () => {
        if (tonConnectUI.connected) {
            setIsLoading(true)
            await tonConnectUI.disconnect()
        } else {
            await tonConnectUI.openModal()
        }
    }

    const formatAddress = (address) => {
        if (address){
            // const tempAddress = Address.parse(address).toString()
            console.log(address)
            // const tempAddress = address?.toString()
            // console.log(tempAddress)
            // return `${tempAddress?.slice(0,4)}...${tempAddress?.slice(-4)}`
            return `${address?.slice(0,4)}...${address?.slice(-4)}`
            // return address
        }
    }

    return (
        <TelegramScreen showbackbutton={true}>
            <TelegramHeader>
                <TelegramText className='telegramTitle'>Wallet </TelegramText>
                <p className='cooming_soon'>Cooming soon...</p>
            </TelegramHeader>

                {isLoading ? (
                    <div>Loading....</div>
                ) : (
                    <div>
                        <h3>Ton Wallet</h3>
                        <p>{formatAddress(tonWalletAddress)}</p>
                        <button className='telegramButton' onClick={handleWalletAction}>Disconnect Wallet</button>
                        <button className='telegramButton' onClick={handleWalletAction}>Connect Wallet</button>
                    </div>
                )}

            <button className='telegramButton' onClick={() => tonConnectUI.sendTransaction(transaction)}>
                Send transaction
            </button>
            
            
            {/* {wallet && (
                <div>
                    <span>Connected wallet: {wallet.name}</span>
                    <span>Device: {wallet.device.appName}</span>
                </div>
            )} */}
            
            <AllTabs/>

        </TelegramScreen>
    );
};

export default WalletScreen;
